/* 4 Template layouts */
#wrap:before, #top:before, .head:before, .Footer:before, #wrap:after, #top:after, .head:after, .Footer:after {
  content: " ";
  display: table;
}
#wrap:after, #top:after, .head:after, .Footer:after {
  clear: both;
}
.Wrap, #wrap {
  margin: 0 auto -20px auto;
  position: relative;
}
#left, #right {
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  width: 250px;
  z-index: 999;
}
#left {
  position: absolute;
  left: -250px;
  height:0;
  overflow:hidden;
}
#right {
  position: fixed;
  top:3px;
  bottom: 0;
  overflow-y: auto;
  right: -250px;
  background-color: #fff;

}
#right .fa-stack
{
  margin-left: -2px;
}
.xs_skin_hide
{
  float: right;
  right: 4px;
  color: #525252;
  font-size: 14px;
  border: 1px solid black;
  padding: 3px 10px;
  border-radius: 5px;

}
.sidebar-left-opened #left {
  left: 0;
  height:auto;
  background-color: #4E4C4C;
}
.sidebar-right-opened {
  position: relative;
  right: 250px;
}
.sidebar-right-opened #right {
  right: 0;
}

.sidebar-right-opened.fixed .navbar-fixed-top {
  right: 0;
}
.right_progressbar_section .progress {
  height: 0.7rem;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  #left {
    position: relative;
    float: left;
    left: auto;
    height:auto;
    background-color: #4E4C4C;

  }
  .sidebar-left-hidden #left {
    width: 0;
    height:0;
    overflow:hidden;
    visibility: hidden;
  }
  .sidebar-left-mini #left {
    width: 50px;
  }

  #content {
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    overflow: hidden;
  }
  .sidebar-right-opened {
    right: 0;
    margin-right: 250px;
  }
  .sidebar-right-opened .navbar-fixed-top {
    margin-right: 250px;
  }
}
@media (min-width: 768px) {
  body.boxed {
    margin-bottom: 0;
  }
  body.boxed .Wrap, body.boxed #wrap, body.boxed .Footer, body.boxed #top .navbar.navbar-fixed-top {
    max-width: 90%;
    margin-left: auto;
    margin-right: auto;
    float: none;
  }
  body.boxed .Footer, body.boxed #footer {
    position: relative;
  }
}
